<template>
  <div class="back">
    <v-breadcrumbs :items="breadList" class="ml-12">
      <template v-slot:divider>
        <v-icon>mdi-chevron-right</v-icon>
      </template>
    </v-breadcrumbs>
    <v-card class="mx-auto mb-12" max-width="1000" min-height="2850">
      <v-form class="pl-8">
        <div class="pt-8"><span class="red--text">*</span></div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder="20"
            :rules="[v => !!v || '']"
            v-model="form.sci.entryname"
          ></v-text-field>
        </v-col>
        <div class="pt-8">
          <span class="red--text">*</span>
        </div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            placeholder=""
            v-model="form.sci.charge"
            :rules="[v => !!v || '']"
          ></v-text-field>
          <v-text-field
            required
            placeholder=""
            :rules="[v => !!v || '']"
            v-model="form.sci.position"
          ></v-text-field>
        </v-col>
        <div class="mt-5">
          <span class="red--text">*</span>
        </div>
        <v-row>
          <v-checkbox
            v-model="form.disciplines"
            class="ml-4 mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
          <v-checkbox
            v-model="form.disciplines"
            class="mx-2"
            value=""
            label=""
          ></v-checkbox>
        </v-row>
        <div class="mt-5">
          <span class="red--text pt-5">*</span>
        </div>
        <v-col cols="12" sm="8">
          <v-textarea
            v-model="form.sci.results"
            filled
            placeholder="200"
            :rules="[v => !!v || '']"
          ></v-textarea>
        </v-col>
        <div class="mt-5">
          <span class="red--text">*</span>
        </div>
        <div style="height: 610px;">
          <quill-editor
            class="mr-8"
            max-height="400"
            v-model="form.sci.tecNew"
            ref="myQuillEditor"
            style="height: 500px;"
            :config="editorOption"
          ></quill-editor>
        </div>
        <div class="mt-5"></div>
        <div style="height: 610px;">
          <quill-editor
            class="mr-8"
            max-height="400"
            v-model="form.sci.future"
            ref="myQuillEditor"
            style="height: 500px;"
            :config="editorOption"
          ></quill-editor>
        </div>
        <div class="mt-5"></div>
        <v-radio-group v-model="form.sci.maturity" row>
          <v-radio class="ml-4" value="" label=""></v-radio>
          <v-radio class="ml-4" value="" label=""></v-radio>
          <v-radio class="ml-4" value="" label=""></v-radio>
          <v-radio class="ml-4" value="" label=""></v-radio>
          <v-radio class="ml-4" value="" label=""></v-radio>
        </v-radio-group>
        <div class="mt-5"><span class="red--text">*</span></div>
        <v-col cols="12" sm="6">
          <v-text-field
            required
            filled
            label=""
            placeholder=""
            :rules="[v => !!v || '']"
            v-model="form.sci.contacts"
          ></v-text-field>
          <v-text-field
            required
            filled
            label=""
            placeholder=""
            :rules="[v => !!v || '']"
            v-model="form.sci.tel"
          ></v-text-field>
          <v-text-field
            required
            filled
            label=""
            placeholder=""
            :rules="[v => !!v || '']"
            v-model="form.sci.mail"
          ></v-text-field>
          <v-text-field
            required
            filled
            label=""
            placeholder=""
            :rules="[v => !!v || '']"
            v-model="form.sci.information"
          ></v-text-field>
        </v-col>
        <div class="mt-5"></div>
        <el-upload
          class="upload-demo mt-5 mr-5"
          drag
          action="https://jsonplaceholder.typicode.com/posts/"
          multiple
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">
            
            <em></em>
          </div>
          <div class="el-upload__tip" slot="tip">
            pdf20M
          </div>
        </el-upload>
        <v-row class="mt-12 mb-12">
          <div class="mr-12 ml-3">
            <v-btn
              width="120px"
              depressed
              large
              color="info"
              @click="releaseSci()"
              :loading="loading"
              :disabled="loading"
              ></v-btn
            >
          </div>
          <div class="mr-12">
            <v-btn
              width="120px"
              class="white--text"
              depressed
              large
              color="#AAAAAA"
              @click="cancel()"
              ></v-btn
            >
          </div>
        </v-row>
      </v-form>
    </v-card>
  </div>
</template>

<script>
import { quillEditor, Quill } from 'vue-quill-editor'
import { container, ImageExtend, QuillWatch } from 'quill-image-extend-module'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

Quill.register('modules/ImageExtend', ImageExtend)

export default {
  name: 'ReleaseScience',
  components: {
    quillEditor
  },
  computed: {},
  data() {
    return {
      loading: false,
      content: null,
      elseMaturity: false,
      editorOption: {
        placeholder: '',
        theme: 'snow', // or 'bubble'
        modules: {
          ImageExtend: {
            loading: true,
            name: 'img',
            size: 3
          },
          toolbar: {
            container: container,
            handlers: {
              image: function() {
                QuillWatch.emit(this.quill.id)
              }
            }
          }
        }
      },
      form: {
        sci: {
          entryname: '',
          tel: '',
          mail: '',
          position: '',
          information: '',
          charge: '',
          results: '',
          maturity: '',
          contacts: '',
          tecNew: null,
          future: null
        },
        disciplines: []
      },
      breadList: [
        {
          text: '',
          disabled: false,
          href: '/university'
        },
        {
          text: '',
          disabled: true
        }
      ]
    }
  },
  methods: {
    clearInput() {
      this.form = {
        sci: {
          entryname: '',
          tel: '',
          mail: '',
          position: '',
          information: '',
          charge: '',
          results: '',
          maturity: '',
          tecNew: null,
          future: null
        },
        disciplines: []
      }
    },
    async releaseSci() {
      let res = await this.$http.post('/api/school/add', this.form, {
        headers: { 'Content-Type': 'application/json' }
      })
      if (res.data.code === 200) {
        this.$message.success('')
        this.clearInput()
        this.$router.push('/university')
      } else {
        this.clearInput()
        this.$message.error('')
      }
      this.clearInput()
    },
    cancel() {
      this.clearInput()
      this.$router.push('/university')
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
